html,body{position:relative;width:100%;height:100%;overflow:hidden;text-size-adjust:100%;-webkit-tap-highlight-color:rgba(0,0,0,0);-webkit-overflow-scrolling:touch}body,ul,li,dl,dt,dd,h1,h2,h3,h4,p,form,table,tr,td{margin:0;padding:0;font-size:0.28125rem;vertical-align:top;border:0}ul li{list-style:none}a,a:hover{text-decoration:none;cursor:pointer}img{vertical-align:middle;border:0}table{border-collapse:collapse;border-spacing:0}::-webkit-selection{background:0}::selection{background:0}*:not(input,textarea){-webkit-touch-callout:none;user-select:none;user-select:none}#root{position:relative;width:100%;height:100%}img{-webkit-user-drag:none;user-drag:none}