@keyframes spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.wrap{height:calc(100% - 1.25rem)}.wrap .main{position:relative;padding:0.46875rem 0.39063rem 0;height:100%;box-sizing:border-box}.wrap .main .menus{padding:0 0.46875rem;border-radius:0.34375rem;background-color:#fff;box-shadow:0 0.07813rem 0.23438rem rgba(106,141,179,.4)}.wrap .main .menus li{position:relative;display:flex;justify-content:flex-start;align-items:center;height:1.40625rem;border-bottom:0.01563rem solid #dae4ef;color:#242424;font-size:0.4375rem;font-weight:normal}.wrap .main .menus li .icon{margin:0 0.3125rem 0 0.07813rem;width:0.53125rem;height:0.53125rem;background-image:url("../../assets/menu_icons.png");background-repeat:no-repeat;background-size:100% 300%}.wrap .main .menus li::before{position:absolute;top:50%;right:0;transform:translateY(-50%);content:" ";width:0.3125rem;height:0.46875rem;background-image:url("../../assets/menu_arrow.png");background-repeat:no-repeat;background-size:100% 100%}.wrap .main .menus li:nth-child(2) .icon{background-position-y:50%}.wrap .main .menus li:nth-child(3) .icon{background-position-y:100%}.wrap .main .links{position:absolute;bottom:0.78125rem;left:50%;transform:translateX(-50%);display:flex;justify-content:space-around;align-items:center;width:9.21875rem;height:2.34375rem;border-radius:0.34375rem;background-color:#fff;box-shadow:0 0.07813rem 0.23438rem rgba(106,141,179,.4)}.wrap .main .links>i{width:1.65625rem;height:1.65625rem;background-image:url("../../assets/link_icons.png");background-repeat:no-repeat;background-size:100% 300%}.wrap .main .links>i.twitter{background-position-y:0}.wrap .main .links>i.facebook{background-position-y:50%}.wrap .main .links>i.discord{background-position-y:100%}